.tabComponent {
  display: block;
  margin-bottom: 8px;
  align-items: center;
  color: #f8f8f2;
  background-color: #191A21; }

.path {
  padding: 4px 10px 6px;
  color: #6272A4;
  background: #44475A70;
  box-shadow: 0 8px 6px -6px black, -8px 5px 6px -6px black, 8px 5px 6px -6px black;
  display: flex; }

.path_text {
  margin-right: 8px; }
  .path_text:hover {
    color: #f8f8f2;
    cursor: pointer; }

.xBttn {
  color: #f8f8f2; }
  .xBttn.shadow {
    visibility: hidden;
    color: #6272A4; }

.tab {
  background-color: transparent;
  color: #f8f8f2;
  border: none;
  outline: none;
  font-size: 15px;
  font-family: Consolas, 'Courier New', monospace;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background: #44475A70;
  border-top: 1px solid #BD93F9;
  display: flex;
  border-right: 1px solid #191A21; }
  .tab.shadow {
    background-color: #21222C;
    color: #6272A4;
    border-top: 1px solid transparent; }
  .tab:hover .xBttn {
    visibility: visible; }

.tabLabel {
  margin: auto; }

.tabBar {
  display: flex;
  overflow: auto;
  overflow-y: hidden; }

.tabBttn {
  background-color: transparent;
  color: #f8f8f2;
  border: none;
  outline: none;
  font-size: 15px;
  font-family: Consolas, 'Courier New', monospace;
  cursor: pointer; }

.link {
  color: #f8f8f2;
  text-decoration: none;
  padding: 10px 10px; }

.linkX {
  display: flex;
  margin: auto 5px auto 0px; }
  .linkX.dis {
    pointer-events: none; }

::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; }

.wBttns {
  color: #f8f8f2;
  background: #44475A70;
  width: 600px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  font-size: 13px; }

.wIcons {
  cursor: pointer;
  padding: 4px 6px; }
  .wIcons:hover {
    background-color: #424450; }
  .wIcons.red:hover {
    background-color: #ff5555; }

.wTitle {
  margin: auto; }

.wIconsWrapper {
  display: flex; }

.wSpace {
  width: 96px; }

.homepage {
  /*width: 100%;*/
  height: 200px;
  margin: 0.25em; }

.line {
  display: flex;
  height: 19px; }

.n_line {
  padding: 0px 10px;
  width: 20px;
  text-align: end;
  margin-right: 10px; }

.key_text {
  color: #8be9fd; }
  .key_text.moved {
    padding-left: 15px;
    border-left: 1px solid #44475a; }

.label_text {
  color: #f1fa8c;
  margin-left: 3px; }

.dots {
  color: #ff79c6;
  margin-right: 5px;
  margin-left: 1px; }

.dash {
  color: #ff79c6;
  margin-right: 5px; }

.page {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content; }

.window {
  position: absolute;
  left: calc(50vw - (600px / 2));
  top: calc(20vw - (330px / 2));
  height: 330px;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  margin: auto;
  width: 600px;
  padding-bottom: 30px;
  background-color: #313241;
  border-bottom: 1px solid #BD93F9; }

body {
  font-family: Consolas, 'Courier New', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  font-size: 15px;
  color: #f8f8f2;
  overflow: hidden; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

html {
  scroll-behavior: smooth; }

